@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --foreground-rgb: 0, 0, 0;
  --background-start-rgb: 214, 219, 220;
  --background-end-rgb: 255, 255, 255;
}

@media (prefers-color-scheme: dark) {
  :root {
    --foreground-rgb: 255, 255, 255;
    --background-start-rgb: 0, 0, 0;
    --background-end-rgb: 0, 0, 0;
  }
}

body {
  color: rgb(var(--foreground-rgb));
  background: linear-gradient(
      to bottom,
      transparent,
      rgb(var(--background-end-rgb))
    )
    rgb(var(--background-start-rgb));
}

h2 {
  padding-top: 2vh;
  font-size: large;
  font-weight: bold;

}

@layer utilities {
  .text-balance {
    text-wrap: balance;
  }
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

/* Container for the effect */
.blurry-transition {
  position: relative;
  width: 100%; /* Adjust width as needed */
  height: 200px; /* Adjust height as needed */
  background: rgba(255, 255, 255, 0.5); /* Background color */
  backdrop-filter: blur(10px); /* Initial blur effect */
  transition: backdrop-filter 0.5s ease-in-out, opacity 0.5s ease-in-out;
  opacity: 1; /* Fully visible */
}

/* Class to trigger the transition */
.blurry-transition.expanded {
  backdrop-filter: blur(0); /* Remove blur */
  opacity: 0; /* Fade out */
}
